function NavIcon(props) {
    const [number, setNumber] = useState(props.number);
    return (
        <div className="nav-icon" onClick={() => setNumber(0)}>
            {props.svg}
            {number ? <div className="nav-icon-top">{number}</div> : undefined}
        </div>
    );
}

